import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import { Button } from '@material-ui/core';
import { Link } from 'react-router-dom';


class AppToolbar extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      mobileOpen: false,
      appBar: {
        backgroundColor: 'rgb(0,0,0,0)',
        color: 'white'
      },
      links: {
        color: 'white'
      }
    };
    this.toolbarMostChangeAspect = this.toolbarMostChangeAspect.bind(this);
  }
  componentDidMount() {
    if (this.props.behavior) {
      window.addEventListener("scroll", this.toolbarMostChangeAspect);
    }
  }
  toolbarMostChangeAspect() {
    const { behavior } = this.props;
    const changeColorOnScroll = behavior.changeColorOnScroll;
    const windowsScrollTop = window.pageYOffset;
    if (windowsScrollTop > changeColorOnScroll.height) {
      this.setState({
        appBar: {
          backgroundColor: 'white',
          color: 'black',
          transition: 'color 500ms linear'
        },
        link: {
          color: 'black',
          transition: 'color 300ms linear'
        }
      });
    } else {
      this.setState({
        appBar: {
          backgroundColor: 'rgb(0,0,0,0)',
          color: 'white',
          transition: 'color 500ms linear'
        },
        link: {
          color: 'white',
          transition: 'color 500ms linear'
        }
      });
    }
  }
  render() {
    const { classes } = this.props;
    return (
      <div className={classes.root} id="AppToolBar">
        <AppBar position="fixed"  style={{...this.state.appBar}}>
          <Toolbar>
            <IconButton className={classes.menuButton} color="inherit" aria-label="Menu">
              <MenuIcon />
            </IconButton>
            <Link className={classes.link} to="">
              <Button  className={classes.button} style={{...this.state.link}}>Home</Button>
            </Link>
            <Link className={classes.link} to="/skills">
              <Button  className={classes.button} style={{...this.state.link}}>Skills</Button>
            </Link>
            <Link className={classes.link} to="/works">
              <Button  className={classes.button} style={{...this.state.link}}>Works</Button>
            </Link>
            <Link className={classes.link} to="/research">
              <Button  className={classes.button} style={{...this.state.link}}>Research</Button>
            </Link>
            <Link className={classes.link} to="/contact">
              <Button  className={classes.button} style={{...this.state.link}}>Contact</Button>
            </Link>
          </Toolbar>
        </AppBar>
      </div>
    );

  }
  
}

const styles = {
  root: {
    flexGrow: 1,
  },
  grow: {
    flexGrow: 1,
  },
  appBar: {
    backgroundColor: 'rgb(0,0,0,0)'
  },
  menuButton: {
    marginLeft: -12,
    marginRight: 20,
    height: '70px',
    width: '100px',
    borderRadius: 0,
  },
  button: {
    color: 'white',
    width: '100%',
    height: '100%',
    borderRadius: 0,
  },
  link: {
    height: '70px',
    width: '200px',
    color: 'white',
    margin: 0,
    textDecoration: 'none',
  } 
};


AppToolbar.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(AppToolbar);